<template>
  <div class="box">
    <div class="item1" v-if="item2show">{{ data[conut][0] }}</div>
    <div class="item2" v-if="item2show">
      <div @click="itemclick(option[conut][0])">A:{{ option[conut][0] }}</div>
      <div @click="itemclick(option[conut][1])">B:{{ option[conut][1] }}</div>
      <div @click="itemclick(option[conut][2])">C:{{ option[conut][2] }}</div>
      <div @click="itemclick(option[conut][3])">D:{{ option[conut][3] }}</div>
      <div v-if="clickshow"><span v-if="click">回答正确</span><span v-else>回答错误</span></div>

    </div>
    <div class="item3"><span>答对{{correct}}</span></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: [["element", "成分"],["element2", "角度2"]],
      option: 
       [["成分", "角度", "单价", "导航"],["成分2", "角度2", "单价2", "导航2"]]
      ,
      correct:0,
      conut:0,
      click:true,
      clickshow:false,
      item2show:true
    };
  },
  methods:{
    itemclick(isoption){
      this.clickshow = true
      if(isoption == this.data[this.conut][1]){
          this.click = true
          this.correct ++
      }else{
        this.click = false      }

  console.log(isoption);
      setTimeout(() => {
         this.clickshow = false
         if (this.conut < this.data.length -1) {
            this.conut ++
         }else{
          this.item2show = false
         }
        //  this.conut ++
      }, 1500);
    }
  }
};
</script>

